// 引入通用table组件
import VXETable from '@/components/common/Table.vue';
// import { showSuccessMessage, showWarningMessage } from '@/utils/message';
import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import SearchBox from '@/components/common/SearchBox';
import {
  STATUS_CODE,
  TITLE,
  VARIABLE,
  BTN_TEXT,
  EQUIPMENT_LIVE
  // DEVICE_STATE
} from '@/utils/constantsFile';
import { getPagePeoplePerformanceReport } from '@/api/personnelperformance';
// import { exportPeoplePerformanceReport } from '@/api/export';
import CustomColumn from '@/components/common/CustomColumn';
// import tableChange from '@/views/equipment/EquipmentStatus/tableChange.vue';
export default {
  components: {
    // 加载通用table组件
    VXETable,
    SearchBox,
    CustomColumn
    // tableChange
  },
  props: {
    groupType: String,
    isSearchBoxShow: {
      type: Boolean
    },
    showInfo: {
      type: Function,
      default: null
    }
  },
  data() {
    return {
      VARIABLE: VARIABLE,
      EQUIPMENT_LIVE: EQUIPMENT_LIVE,
      BTN_TEXT: BTN_TEXT,
      deviceName: '',
      dialogVisible: false,
      hackReset: false,
      // 查询字段
      srearchList: [],
      orderByData: {},
      // 可编辑配
      editConfig: {
        trigger: 'click',
        mode: 'row',
        showStatus: false
        // icon: 'fa fa-file-text-o'
      },
      // 查询字段
      searchData: [],
      // 显示那个表
      showTable: true,
      // table是否加载
      tableLoadings: false,
      // 控制自定义列模态框显示
      dialogVisibleCustomColumn: false,
      // 打印数据
      outData: [],
      operatorJorNumber: '',
      month: '',
      userName: '',
      // table数据
      tableDatas: [],
      // table表头配置
      tableColumn: [],
      tableColumns: [
        {
          field: 'month',
          title: '月份',
          width: '',
          align: 'center',
          visible: true
        },
        {
          field: 'userName',
          title: '姓名',
          width: '',
          align: 'center'
        },
        {
          field: 'operatorJorNumber',
          title: '工号',
          width: '',
          align: 'center'
        },
        {
          field: 'roleName',
          title: '岗位',
          width: '',
          align: 'center',
          sortable: true
        },
        {
          field: 'workNum',
          title: '工作数量',
          width: '',
          align: 'center',
          sortable: true
        },
        {
          field: 'workTime',
          title: '工作时长',
          formatter: 'timeStampSeconds',
          width: '',
          align: 'center',
          sortable: true
        },
        // {
        //   field: 'maintenanceTime',
        //   title: EQUIPMENT_LIVE.ESTIMATED_MAINTENANCE_TIME,
        //   formatter: ['formatDate', 'yyyy-MM-dd'],
        //   width: '150',
        //   align: 'center',
        //   sortable: true
        // },
        {
          field: '',
          title: BTN_TEXT.OPERATION,
          width: '100',
          align: 'center',
          fixed: 'right',
          // type: 'html',
          slots: {
            // header: ({ column }) => {
            //   return [
            //     <span>
            //       <span>{column.title}</span>
            //       <i
            //         onClick={() => this.showCustomColumn()}
            //         class="el-icon-s-tools set-btn"
            //       ></i>
            //     </span>
            //   ];
            // },
            default: ({ row, column, cellValue }) => {
              return [
                <div>
                  <el-button type="text" onClick={() => this.goInfo(row)}>
                    绩效明细
                  </el-button>
                </div>
              ];
            }
          }
        }
      ],
      typeOptions: [],
      groupOptions: [],
      typeValue: '',
      groupValue: '',
      // table分页配置
      tablePages: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        align: 'right',
        pageSizes: [10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true
      }
    };
  },
  watch: {},
  created() {
    // 获取查询参数
    // this.GetListQueryParameter();
    // 调用列表查询接口
    this.findList();
    // // 类型下拉数据
    // this.getTypeTree();
    // 车间分组下拉数据
    // this.getGroupTree();
    // this.tableColumn = JSON.parse(JSON.stringify(this.tableColumns));
  },
  methods: {
    /**
     * @description:获取表格数据
     * @param
     * @return:
     * @author: CH
     */
    async findList() {
      var dateTiem = '';
      if (this.month !== '') {
        dateTiem = this.$utils.toDateString(this.month, 'yyyy-MM');
      }

      try {
        this.tableLoadings = true;
        const res = await getPagePeoplePerformanceReport({
          size: this.tablePages.pageSize,
          current: this.tablePages.currentPage,
          month: dateTiem,
          operatorJorNumber: this.operatorJorNumber,
          userName: this.userName
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将请求数据写入table
          this.tableDatas = res.rows;
          for (var i in this.tableDatas) {
            if (this.tableDatas[i].fileUrl !== undefined) {
              this.tableDatas[i].fileUrl =
                this.$store.state.publicUrl +
                this.tableDatas[i].fileUrl.substring(1);
            }
          }
          // 将总条数写入table分页配置
          this.tablePages.total = res.total;
          // 加载动画消失
          this.tableLoadings = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },

    /**
     * @description: 执行table分页
     * @param currentPage 第几页
     * @param pageSize 一页几条
     * @return: 无
     * @author: CH
     */
    onTablePageChanges(currentPage, pageSize) {
      // 点击页数写入table分页配置
      this.tablePages.currentPage = currentPage;
      // 一页条数夜路table分页配置
      this.tablePages.pageSize = pageSize;
      // 调用查询接口
      this.findList();
    },
    /**
     * @description: 查询
     * @param obj ztree对象
     * @return: 无
     * @author: ZJM
     */
    search() {
      this.onTablePageChanges(1, 10);
    },
    /**
     * @description: 重置
     * @param {type}
     * @return:
     * @author: CH
     */
    reset() {
      this.month = '';
      this.userName = '';
      this.operatorJorNumber = '';
      this.onTablePageChanges(1, this.tablePages.pageSize);
    },
    /**
     * @description: 进入详情页面
     * @param obj ztree对象
     * @return: 无
     *@author: ZJM
     */
    goInfo(row) {
      this.showInfo(1, row);
    },
    /**
     * @description:导出
     * @param {type}
     * @return:
     * @author: CH
     */
    async exportTable() {
      // try {
      //   const res = await exportPeoplePerformanceReport({
      //     month: this.month,
      //     operatorJorNumber: this.operatorJorNumber,
      //     userName: this.userName
      //   });
      //   if (res.data.code !== undefined) {
      //     showWarningMessage(res.message);
      //   } else {
      //     this.$options.filters.setExportFunc(res, '人员绩效报表.xls');
      //   }
      // } catch (error) {
      //   showErrorNotify(this.GLOBAL.TITLE.NOTIFY, error);
      // } finally {
      // }
      var dateTiem = '';
      if (this.month !== '') {
        dateTiem = this.$utils.toDateString(this.month, 'yyyy-MM');
      }

      try {
        this.tableLoadings = true;
        const res = await getPagePeoplePerformanceReport({
          size: 99999999,
          current: this.tablePages.currentPage,
          month: dateTiem,
          operatorJorNumber: this.operatorJorNumber,
          userName: this.userName
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将请求数据写入table
          // this.tableDatas = res.rows;
          // for (var i in this.tableDatas) {
          //   if (this.tableDatas[i].fileUrl !== undefined) {
          //     this.tableDatas[i].fileUrl =
          //       this.$store.state.publicUrl +
          //       this.tableDatas[i].fileUrl.substring(1);
          //   }
          // }
          // // 将总条数写入table分页配置
          // this.tablePages.total = res.total;
          // 加载动画消失
          this.tableLoadings = false;
          this.$refs.xGrid.exportData('人员绩效报表', res.rows);
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    }
  }
};
