// 引入组件
import deviceInfo from '@/views/report/PersonnelPerformance/personInfo.vue';
import deviceTable from '@/views/report/PersonnelPerformance/personTable.vue';
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
import { EQUIPMENT_LIVE, BTN_TEXT } from '@/utils/constantsFile';
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
import SearchBox from '@/components/common/SearchBox';
// // 引入审核通过组件
// import waterMeter from '@/views/main/EnergyAllocation/waterMeter.vue';
// // 引入审核未通过组件
// import gasMeter from '@/views/main/EnergyAllocation/gasMeter.vue';
export default {
  name: 'PersonnelPerformance',
  data() {
    return {
      // 搜索框有关
      isSearchBoxShow: false,
      // tab默认激活下标
      BTN_TEXT: BTN_TEXT,
      EQUIPMENT_LIVE: EQUIPMENT_LIVE,
      active: 0,
      // rows: {},
      showFlag: 0,
      personData: {},
      hideMain: true,
      // 默认引入的组件名称
      currentView: 'deviceTable',
      dialogVisible: false
      // tabs: [
      //   {-
      //     type: 'deviceTable',
      //     name: EQUIPMENT_LIVE.OPERATION_MONITORING,
      //     value: '2',
      //     views: 'deviceTable'
      //   },
      //   {
      //     type: 'warningTable',
      //     name: EQUIPMENT_LIVE.ALARM_MONITORING,
      //     value: '3',
      //     views: 'warningTable'
      //   }
      // ]
    };
  },
  created() {},
  methods: {
    /**
     * @description: （主页面）点击“搜索”按钮，打开搜索框
     * @param 无
     * @return: void
     * @author: cyb
     */
    showSearchCard() {
      // alert(1321);
      this.isSearchBoxShow = !this.isSearchBoxShow;
    },
    /**
     * @description: 切换tab事件
     * @param i 下标
     * @param v 组件名
     * @return: 无
     * @author: ZJM
     */
    toggle(i, v) {
      this.active = i;
      this.currentView = v;
      // this.groupType = v;
    },
    /**
     * @description:显示详情也卖弄
     * @param {type}
     * @return:
     * @author: CH
     */
    showInfo(showFlag, row) {
      this.showFlag = showFlag;
      this.personData = row;
      this.hideMain = false;
    },
    /**
     * @description:返回主页面
     * @param {type}
     * @return:
     * @author: CH
     */
    back() {
      this.showFlag = 0;
      this.hideMain = true;
    },
    /**
     * @description:导出
     * @param {type}
     * @return:
     * @author: CH
     */
    exportTable() {
      if (this.active === 0) {
        this.$refs.deviceTable.exportTable();
      } else {
        this.$refs.warningTable.exportTable();
      }
    },
    /**
     * @description:   刷新
     * @param {type}
     * @return:
     * @author: CH
     */
    refresh() {
      this.$refs.deviceTable.findList();
    }
  },
  components: {
    // 加载未审核组件
    deviceTable,
    HeaderTitle,
    deviceInfo,
    HeadOperationItem,
    SearchBox
  }
};
