// 引入通用table组件
import VXETable from '@/components/common/Table.vue';
// 引入未审核组件
import deviceTable from '@/views/equipment/EquipmentStatus/deviceTable.vue';
import parametricLiineChart from '@/views/equipment/EquipmentStatus/parametricLiineChart.vue';
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import XEUtils from 'xe-utils'; // 注： 在别的组件里调用xe-utils要用this.$utils
import {
  STATUS_CODE,
  TITLE,
  BTN_TEXT,
  DEVICE_STATE,
  EQUIPMENT_LIVE
} from '@/utils/constantsFile';
// 导入日期处理类库
import { List } from 'vxe-table';
import { getPagePeoplePerformanceReportDetail } from '@/api/personnelperformance';

export default {
  name: 'tabs',
  props: {
    personData: List,
    back: {
      type: Function,
      default: null
    }
  },
  data() {
    return {
      // tab默认激活下标
      BTN_TEXT: BTN_TEXT,
      DEVICE_STATE: DEVICE_STATE,
      EQUIPMENT_LIVE: EQUIPMENT_LIVE,
      active: 0,
      rows: {},
      deivceShowFlag: 0,
      title: '',
      showData: {},
      showDataInfo: [],
      parameterData: [],
      // 默认引入的组件名称
      currentView: 'deviceTable',
      dialogVisible: false,
      hackReset: false,
      editConfig: {
        trigger: 'click',
        mode: 'row',
        showStatus: false
        // icon: 'fa fa-file-text-o'
      },
      // table是否加载
      tableLoadings: false,
      // 打印数据
      outData: [],
      // table数据
      tableDatas: [],
      // table表头配置
      tableColumns: [
        {
          field: 'time',
          title: '日期',
          formatter: ['formatDate', 'yyyy-MM-dd'],
          align: 'center'
        },
        {
          field: 'taskCode',
          title: '任务单号',
          align: 'center'
        },
        {
          field: 'materialsCode',
          title: '物料编号',
          align: 'center'
        },
        {
          field: 'materialsName',
          title: '物料名称',
          align: 'center'
        },
        {
          field: 'workNum',
          title: '工作数量',
          align: 'center'
        },
        {
          field: 'startTime',
          title: '开工时间',
          formatter: ['formatDate', 'yyyy-MM-dd HH:mm:ss'],
          align: 'center'
        },
        {
          field: 'endTime',
          title: '完工时间',
          formatter: ['formatDate', 'yyyy-MM-dd HH:mm:ss'],
          align: 'center'
        },
        {
          field: 'workTime',
          title: '工作时长',
          formatter: 'timeStampSeconds',
          align: 'center'
        }
      ],
      // table分页配置
      tablePages: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        align: 'right',
        pageSizes: [10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true
      },
      timeOptionRange: ''
    };
  },
  watch: {},
  created() {
    // 调用列表查询接口
    this.title = '绩效明细' + '       ' + this.personData.userName;
    this.findList();
  },
  methods: {
    /**
     * @description: 进入详情页面
     * @param obj ztree对象
     * @return: 无
     *@author: CH
     */
    backMain() {
      this.back();
    },
    /**
     * @description:获取24小时表格数据
     * @param
     * @return:
     * @author: CH
     */
    async findList() {
      var date = new Date(this.personData.month);
      var nowMonth = date.getMonth(); // 当前月
      var nowYear = date.getFullYear(); // 当前年

      // 本月的开始时间
      var monthStartDate = new Date(nowYear, nowMonth, 1);
      // 本月的结束时间
      var monthEndDate = new Date(nowYear, nowMonth + 1, 0);
      var timeStar = new Date(monthStartDate).getDate() < 10 ? '0' + new Date(monthStartDate).getDate() : new Date(monthStartDate).getDate();
      var timeEnd = new Date(monthEndDate).getDate();

      try {
        this.tableLoadings = true;
        const res = await getPagePeoplePerformanceReportDetail({
          size: this.tablePages.pageSize,
          current: this.tablePages.currentPage,
          userId: this.personData.userId,
          startTime: this.personData.month + '-' + timeStar + ' 00:00:00',
          endTime: this.personData.month + '-' + timeEnd + ' 23:59:59'
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将请求数据写入table
          this.tableDatas = res.rows;

          // 将总条数写入table分页配置
          this.tablePages.total = res.total;
          // 加载动画消失
          this.tableLoadings = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 执行table分页
     * @param currentPage 第几页
     * @param pageSize 一页几条
     * @return: 无
     * @author: CH
     */
    onTablePageChanges(currentPage, pageSize) {
      // 点击页数写入table分页配置
      this.tablePages.currentPage = currentPage;
      // 一页条数夜路table分页配置
      this.tablePages.pageSize = pageSize;
      // 调用查询接口
      this.findList();
    },

    /**
     * @description:关闭弹窗
     * @param {type}
     * @return:
     * @author: CH
     */
    handleClose() {
      this.dialogVisible = false;
      this.hackReset = false;
    },
    /**
     * @description:刷新
     * @param {type}
     * @return:
     * @author: CH
     */
    refresh() {
      // 点击页数写入table分页配置
      this.tablePages.currentPage = 1;
      // 一页条数夜路table分页配置
      this.tablePages.pageSize = 10;
      this.findList();
    }
  },
  components: {
    // 加载未审核组件
    deviceTable,
    HeaderTitle,
    HeadOperationItem,
    parametricLiineChart,
    VXETable,
    XEUtils
  }
};
